body {
 
  font-family: "Stick No Bills", sans-serif;
  font-family: "Ubuntu", sans-serif;
}
.App {

}


@media all and (min-width: 801px) {
  .nav-app {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 10px;

    .header-title {
      font-size: 40px;

      .hamburger-menu {
        display: none;
      }
      a {
        text-decoration: none;
        color: black;
      }
      font-weight: bold;
    }
    .header-list {
      display: flex;
      align-items: center;

      li {
        list-style: none;
        margin-left: 20px;

        .link {
          color: black;
          text-decoration: none;
          transition: 0.2s ease-in-out;
          padding: 10px;
        }

        .link:hover {
          color: #0649f0;
          transition: 0.2s ease-in-out;
        }
      }
    }
  }
}

@media all and (max-width: 800px) {
  .nav-app {
    position: sticky;
    top: 0;
    display: block;
    justify-content: center;
    align-items: center;

    padding-top: 10px;
    .header-title {
      display: flex;
      justify-content: space-between;
      font-size: 40px;

      .hamburger-menu {
        margin-right: 10px;
        cursor: pointer;


        

      }
      a {
        text-decoration: none;
        color: black;
      }
      font-weight: bold;
    }
    .hide {
      display: none;
      transition: 0.2s ease-in-out;

    }
    .header-list {
      text-align: center;
      justify-content: center;
      transition: 0.2s ease-in-out;

      li {
        padding: 10px;
        list-style: none;
        transition: 0.2s ease-in-out;
        .link {
          color: black;
          text-decoration: none;
          transition: 0.2s ease-in-out;
          padding: 10px;
        }

        .link:hover {
          color: #0649f0;
          transition: 0.2s ease-in-out;
        }
      }
    }
  }
}
